import { Module } from 'vuex'
import { ILoginState } from './types'
import { IRootState } from '../types'
import { accountLoginAction } from '@/service/login/login'
import { ILoginResult } from '@/service/login/type'
import LocalCache from '@/utils/cache/cache'
import router from '@/router'
import { mapMenusToRoutes } from '@/utils/map-menus'
import { userMenus } from './login.config'
import { IDataType } from '@/service/types'

const loginModule: Module<ILoginState, IRootState> = {
  namespaced: true,
  state() {
    return {
      token: '',
      userMenus: []
    }
  },
  getters: {},
  actions: {
    async accountLoginAction({ commit }, payload: any) {
      const loginResult: IDataType<ILoginResult> = await accountLoginAction(
        payload
      )
      console.log(loginResult)
      const { token } = loginResult.data
      commit('changeToken', token)
      LocalCache.setCache('token', token)

      commit('changeUserMenus', userMenus)
      router.push('/main')
    },
    loadLocalLogin({ commit }) {
      const token = LocalCache.getCache('token')
      if (token) {
        commit('changeToken', token)
      }
      const userMenus = LocalCache.getCache('userMenus')
      if (userMenus) {
        commit('changeUserMenus', userMenus)
      }
    }
  },
  mutations: {
    changeToken(state, token: string) {
      state.token = token
    },
    changeUserMenus(state, userMenus: []) {
      state.userMenus = userMenus

      // userMenus => routes
      const routes = mapMenusToRoutes(userMenus)
      // routes => router.main.children
      routes.forEach((route) => {
        router.addRoute('main', route)
      })
    }
  }
}

export default loginModule
