import Request from '@/service'
import { IDataType } from '@/service/types'

export function getPageListData(url: string, queryInfo: any) {
  console.log(url)
  console.log(queryInfo)
  return Request.post<IDataType>({
    url: url,
    data: queryInfo
  })
}

// url: /user/id
export function deletePageData(url: string) {
  return Request.delete<IDataType>({
    url: url
  })
}

export function createPageData(url: string, data: any) {
  return Request.post<IDataType>({
    url: url,
    data: data
  })
}

export function updatePageData(url: string, data: any) {
  console.log(url)
  console.log(data)
  return Request.put<IDataType>({
    url: url,
    data: data
  })
}

export function handleStopAction(url: string, data: any) {
  console.log(data)
  // topic/stop
  return Request.post({
    url: url,
    data: data
  })
}
